.Header
  width: 100%
  display: flex
  align-items: center
  gap: 20px
  h4
    flex: 100%
    padding: 5px
    font-size: 42px !important
    text-align: center
    border: 1px solid black
    border-radius: 5px
    box-shadow: 4px 4px black
  button
    padding: 13px

@media (max-width: 600px) 
  .Header
    h4
      font-size: 30px !important
    button
      padding: 9px